<template lang="pug">
  .news-add-form
    form.news-add__main(action="#" @submit.prevent="" ref="form")
      .news-add__text
        input.news-add__text-title(type="text" placeholder=" " v-model="title")
        editor-content.news-add__text-main(:editor="editor")
        editor-menu-bar.news-add__actions(:editor="editor" v-slot="{ commands, isActive, getMarkAttrs, menu }" @mouseleave.native="hideLinkMenu")
          .news-add__actions-buttons
            button.bold(:class="{ 'is-active': isActive.bold() }" @click="commands.bold") 
            button.italic(:class="{ 'is-active': isActive.italic() }" @click="commands.italic") 
            button.underline(:class="{ 'is-active': isActive.underline() }" @click="commands.underline") 
            .news-add__actions-link
              .news-add__actions-link-hidden(:class="{'is-active': isOpenLinkMenu}")
                form(@submit.prevent="setLinkUrl(commands.link, linkUrl)")
                  input(type="text" v-model="linkUrl" placeholder="https://" ref="linkInput" @keydown.esc="hideLinkMenu")
                  button(type="button" @click="setLinkUrl(commands.link, linkUrl)")
                    simple-svg(:filepath="'/static/img/add.svg'")
              button.link(:class="{ 'is-active': isActive.link() }" @click="openLinkMenu(getMarkAttrs('link'))")
                simple-svg(:filepath="'/static/img/link.svg'")
      .news-add__settings
        h4.news-add__settings-title  
        add-tags(:tags="tags" @change-tags="onChangeTags")
        button-hover.news-add__planning(variant="white" bordered @click.native="openModal" v-if="!edit || deffered") 
        button-hover(@click.native="submitForm") 
    modal.news-add__modal(v-model="modalShow")
      h4     
      br
      datetime(first-day-of-week="1" format="DD/MM/YYYY H:i" width="200px" v-model="planingTime")
      template(slot="actions")
        button-hover(@click.native="onPlaning") 
        button-hover(variant="red" bordered @click.native="closeModal") 
</template>

<script>
import { Editor, EditorContent, EditorMenuBar } from 'tiptap'
import { Bold, Italic, Underline, Link } from 'tiptap-extensions'
import { mapGetters, mapActions } from 'vuex'
import moment from 'moment'
import AddTags from '@/components/News/AddTags'
import Modal from '@/components/Modal'
import datetime from 'vuejs-datetimepicker'
export default {
  name: 'NewsAddForm',
  props: {
    edit: Boolean,
    deffered: Boolean,
    info: Object
  },
  components: { AddTags, EditorContent, EditorMenuBar, Modal, datetime},
  data: () => ({
    title: '',
    tags: [],
    editor: null,
    linkUrl: '',
    isOpenLinkMenu: false,
    modalShow: false,
    isPlaning: false,
    planingTime: null,
    componentKey: 0
  }),
  computed: {
    ...mapGetters('profile/info', ['getInfo'])
  },
  methods: {
    ...mapActions('profile/feeds', ['actionsFeed']),
    onPlaning() {
      this.isPlaning = true
      this.submitForm()
    },
    onChangeTags(tags) {
      this.tags = tags
    },
    submitForm() {
      if (this.title.length <= 5) {
        this.modalShow && this.closeModal()
        alert('      ')
        return
      }
      if (this.editor.getHTML().length <= 7) {
        this.modalShow && this.closeModal()
        alert('  ')
        return
      }
      this.actionsFeed({
        route: this.$route.name,
        post_id: this.info ? this.info.id : null,
        edit: this.edit,
        id: this.getInfo.id,
        title: this.title,
        post_text: this.editor.getHTML(),
        tags: this.tags,
        publish_date:
          this.isPlaning ? moment(this.planingTime, "DD/MM/YYYY HH:mm").valueOf() : false
      }).then(() => {
        this.$emit('submit-complete')
      })
    },
    openLinkMenu(attrs) {
      this.linkUrl = attrs.href
      this.isOpenLinkMenu = true
      this.$nextTick(() => {
        this.$refs.linkInput.focus()
      })
    },
    setLinkUrl(command, url) {
      command({ href: url })
      this.isOpenLinkMenu = false
      this.editor.focus()
    },
    hideLinkMenu() {
      this.linkUrl = null
      this.isOpenLinkMenu = false
    },
    openModal() {
      this.modalShow = true
    },
    closeModal() {
      this.modalShow = false
      this.isPlaning = false
    }
  },
  mounted() {
    if (this.edit) {
      this.title = this.info.title
      this.tags = this.info.tags
      this.editor = new Editor({
        content: `<p>${this.info.post_text}</p>`,
        extensions: [new Bold(), new Italic(), new Underline(), new Link()]
      })
    } else {
      this.editor = new Editor({
        content: '<p>   !</p>',
        extensions: [new Bold(), new Italic(), new Underline(), new Link()]
      })
    }
  },
  beforeDestroy() {
    this.editor.destroy()
  }
}
</script>



