export default [
  {
    path: '/contacting-support',
    name: 'ContactingSupport',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: '   '
    },
    component: () => import('@/pages/Support/ContactingSupport.vue')
  },
  {
    path: '/contacting-support-success',
    name: 'ContactingSupportSuccess',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: '    '
    },
    component: () => import('@/pages/Support/ContactingSupportSuccess.vue')
  },
  {
    path: '/404',
    name: 'PageNotFound',
    meta: {
      layout: 'main',
      title: ' 404'
    },
    component: () => import('@/pages/PageNotFound.vue')
  },
]



