import axios from 'axios'
import router from '@/router'

export default {
  namespaced: true,
  actions: {
    async sendMessage({dispatch}, data) {
      await axios({
        url: 'support/message',
        data: data,
        method: 'POST'
      }).then(response => {
        dispatch('global/alert/setAlert', {
          status: 'success',
          text: '    '
        }, {
          root: true
        })
      })
    },
    toPageNotFound({}, message){
      router.push({ name: 'PageNotFound', params: { message: message } })
    }
  }
}



